import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { SearchFilterProps } from '../../../../hooks/useSearchFilter';
import { CLASS_NAME } from '../../../../models/dtos/ger/cepLocalidade';
import { CLASS_NAME as CLASS_NAME_UF } from '../../../../models/dtos/ger/cepUF';

export interface CepLocalidadeISProps extends InputProps {
  searchFilter?: SearchFilterProps;
}

const CepLocalidadeIS: FC<CepLocalidadeISProps> = props => {
  const searchExpressionsFull = defaultExpressions => {
    const expressions = [];

    if (props.searchFilter) {
      const item = props.searchFilter.getFilter(CLASS_NAME_UF);
      if (item) {
        expressions.push({
          fieldName: 'uf.id',
          value: item.id,
        });
      }
    }

    if (expressions.length > 0) {
      expressions[0].expressions = defaultExpressions;
    } else {
      expressions.push(...defaultExpressions);
    }

    return expressions;
  };

  const onChange = value => {
    if (props.searchFilter) {
      props.searchFilter.setFilter(CLASS_NAME, value);
    }
    if (props.onChange) {
      props.onChange(value);
    }
  };

  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'nome',
            text: 'Nome',
          },
          {
            dataField: 'cep',
            text: 'CEP',
          },
          {
            dataField: 'codigoMunicipio',
            text: 'Código do Município',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchExpressionsFull,
        searchViewId: 'CepLocalidadeSearchView',
      }}
      label={props.label ? props.label : 'Cidade'}
      placeholder={props.placeholder ? props.placeholder : 'Informe a Cidade'}
      type={InputType.INPUT_SEARCH}
      onChange={onChange}
    />
  );
};

export default CepLocalidadeIS;
