import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { SearchFilterProps } from '../../../../hooks/useSearchFilter';
import { CLASS_NAME } from '../../../../models/dtos/ger/cepUF';

export interface CepUFISProps extends InputProps {
  searchFilter?: SearchFilterProps;
}

const CepUFIS: FC<CepUFISProps> = props => {
  const onChange = value => {
    if (props.searchFilter) {
      props.searchFilter.setFilter(CLASS_NAME, value);
    }
    if (props.onChange) {
      props.onChange(value);
    }
  };

  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Código',
          },
          {
            dataField: 'sigla',
            text: 'Sigla',
          },
          {
            dataField: 'nome',
            text: 'Nome',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'CepUFSearchView',
      }}
      label={props.label ? props.label : 'UF'}
      placeholder={props.placeholder ? props.placeholder : 'Informe a UF'}
      type={InputType.INPUT_SEARCH}
      onChange={onChange}
    />
  );
};

export default CepUFIS;
