import React, { FC, useRef } from 'react';
import { useForm } from 'react-hook-form';
import { Alert, Col, Row } from 'reactstrap';
import {
  CellEditProps,
  cepFormatter,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchDataGridRefProps,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import useSearchFilter from '../../../../hooks/useSearchFilter';
import { CLASS_NAME as CLASS_NAME_LOCALIDADE } from '../../../../models/dtos/ger/cepLocalidade';
import { CepLogradouro, CLASS_NAME } from '../../../../models/dtos/ger/cepLogradouro';
import { CLASS_NAME as CLASS_NAME_UF } from '../../../../models/dtos/ger/cepUF';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'nome',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const CepLogradouroSearchView: FC<SearchViewProps<CepLogradouro>> = props => {
  const formProps = useForm<CepLogradouro>({ mode: 'onBlur' });
  const searchRef = useRef<SearchDataGridRefProps>();
  const searchFilter = useSearchFilter();

  const columns: ColumnsProps[] = [
    {
      dataField: 'nome',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Nome',
    },
    {
      dataField: 'bairro.bairro',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Bairro',
    },
    {
      dataField: 'cep',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: cepFormatter,
      sort: true,
      text: 'CEP',
    },
  ];

  const searchExpressionsFull = defaultExpressions => {
    const localidade = searchFilter.getFilter(CLASS_NAME_LOCALIDADE);
    const uf = searchFilter.getFilter(CLASS_NAME_UF);

    let expressions = [];

    if (localidade) {
      expressions = [
        {
          expressions: defaultExpressions,
          fieldName: 'localidade.id',
          value: localidade.id,
        },
      ];
    }

    if (uf) {
      expressions = [
        {
          expressions: expressions.length > 0 ? expressions : defaultExpressions,
          fieldName: 'uf.id',
          value: uf.id,
        },
      ];
    }

    if (expressions.length === 0) {
      expressions = defaultExpressions;
    }

    return expressions;
  };

  const onChangeUf = () => setTimeout(() => searchRef.current.notifySearchExpressionChange(), 200);
  const onChangeLocalidade = () => setTimeout(() => searchRef.current.notifySearchExpressionChange(), 200);

  return (
    <WindowComponent title="Manutenção de Logradouro">
      <Row>
        <Col>
          <Alert color="primary" className="text-center">
            Selecione a UF e a Cidade para efetuar a consulta
          </Alert>
        </Col>
      </Row>
      <Row>
        <Col>
          <CepUFIS formProps={formProps} id="uf" isPrependLabel={true} onChange={onChangeUf} searchFilter={searchFilter} />
        </Col>
        <Col>
          <CepLocalidadeIS formProps={formProps} id="localidade" isPrependLabel={true} onChange={onChangeLocalidade} searchFilter={searchFilter} />
        </Col>
      </Row>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        ref={searchRef}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        searchExpressionsFull={searchExpressionsFull}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'CepLogradouroEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default CepLogradouroSearchView;
